import React, {
  createContext,
  useContext,
  useEffect,
  useReducer,
  useState,
} from 'react';

import AsyncStorage from '@react-native-async-storage/async-storage';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState({
    walletData: null,
    privateKey: null, // Tambahkan privateKey ke dalam nilai default user
  });

  // Fungsi untuk memuat data wallet dari penyimpanan lokal saat aplikasi dimulai
  useEffect(() => {
    const loadWalletData = async () => {
      try {
        const storedAddress = await AsyncStorage.getItem('walletAddress');
        const storedPrivateKey = await AsyncStorage.getItem('privateKey');

        if (storedAddress && storedPrivateKey) {
          const walletData = {
            address: storedAddress,
            privateKey: storedPrivateKey,
          };

          // Set nilai user, termasuk privateKey
          setUser((prevUser) => ({
            ...prevUser,
            walletData,
            privateKey: storedPrivateKey,
          }));
        }
      } catch (error) {
        console.error('Terjadi kesalahan saat memuat data wallet:', error);
      }
    };

    loadWalletData();
  }, []);

  // Fungsi untuk menyimpan data wallet ke penyimpanan lokal
  const saveWalletData = async (address, privateKey) => {
    try {
      await AsyncStorage.setItem('walletAddress', address);
      await AsyncStorage.setItem('privateKey', privateKey);
      const walletData = { address, privateKey };

      // Set nilai user, termasuk privateKey
      setUser((prevUser) => ({
        ...prevUser,
        walletData,
        privateKey,
      }));
    } catch (error) {
      console.error('Terjadi kesalahan saat menyimpan data wallet:', error);
    }
  };

  return (
    <AuthContext.Provider value={{ user, setUser, saveWalletData }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const CartContext = createContext();

const cartReducer = (state, action) => {
  switch (action.type) {
    case 'ADD_TO_CART':
      const existingProduct = state.find(item => item.id === action.payload.id);
      if (existingProduct) {
        return state.map(item =>
          item.id === action.payload.id ? { ...item, quantity: item.quantity + 1 } : item
        );
      } else {
        return [...state, { ...action.payload, quantity: 1 }];
      }
    case 'DECREASE_QUANTITY':
      return state.map(item =>
        item.id === action.payload.id ? { ...item, quantity: item.quantity - 1 } : item
      );
    case 'REMOVE_FROM_CART':
      return state.filter(item => item.id !== action.payload.id);
    case 'CLEAR_CART':
      return []; // Menghapus semua item dari keranjang dengan mengembalikan array kosong
    default:
      return state;
  }
};

export const CartProvider = ({ children }) => {
  const [cartItems, dispatch] = useReducer(cartReducer, []);

  return (
    <CartContext.Provider value={{ cartItems, dispatch }}>
      {children}
    </CartContext.Provider>
  );
};

export const useCart = () => {
  const context = useContext(CartContext);
  if (!context) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
};

const AuthenticatedUserContext = createContext({});

 export const AuthenticatedUserProvider = ({ children }) => {
  const [user, setUser] = useState(null);
return (
    <AuthenticatedUserContext.Provider value={{ user, setUser }}>
      {children}
    </AuthenticatedUserContext.Provider>
  );
};
