import React, {
  useEffect,
  useState,
} from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  onAuthStateChanged,
  signInWithEmailAndPassword,
} from 'firebase/auth';
import {
  Alert,
  Image,
  ImageBackground,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import { auth } from '../Fb';

const Login = ({navigation}) => {
  const [user, setUser] = useState(null);
  const [email,setEmail]=useState('');
  const [password,setPassword]=useState('');
  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      console.log("USER IS STILL LOGGED IN: " , user);
      if (user) {
        setUser(user);
      }
    });
  }, [user]);

  function Masuk(){
    signInWithEmailAndPassword(auth, email, password)
  .then((userCredential) => {
    // Signed in 
    const user = userCredential.user;
    navigation.replace('main');
    Alert.alert('Login Berhasil', '');
  })
  .catch((error) => {
    const errorCode = error.code;
    const errorMessage = error.message;
    console.log('login gagal');
    Alert.alert('email atau password salah');
  });
  }


  return (
    <View style={{
      flex:1
    }}>
    <StatusBar style="auto" />
    <ImageBackground source={require('../assets/baby.jpg')}
    style={{
      flex:1
    }}
    >
      
      <View style={{
        marginTop:'5%',
        justifyContent:'center',
        alignItems:'center'
       }}>
       <Image source={require('../assets/miner.jpg')}
       style={{
        height:140,width:140,borderRadius:20
       }}/>
       </View>
      <View style={{
        justifyContent:'center',
        alignItems:'center',
        marginTop:'10%'
      }}>
      <TextInput style={{
          height:40,
          width:'85%',
          borderWidth:0.8,
          borderColor:'white',
          borderRadius:5,
          marginTop:'5%',
          elevation:10,
          color:'white',
          textAlign:'auto',
          
          
      }}
      placeholder='email'
      placeholderTextColor='white'
      value={ email}
      onChangeText={(text)=>setEmail(text)}
      
      />
      <TextInput   style={{
        height:40,
        width:'85%',
        borderWidth:0.8,
        borderColor:'white',
        borderRadius:5,
        marginTop:'5%',
        elevation:5,
        color:'white'
    }}
    placeholder= '  password'
    placeholderTextColor='white'
    value={ password}
    onChangeText={(text)=>setPassword(text)}
    />
     <TouchableOpacity
     style={{
      justifyContent:'center',alignItems:'center',
      height:40,width:'70%',
      backgroundColor:'#00FFFF',
      borderRadius:10,
      marginTop:'5%',
      margin:5,
      elevation:10
    }}
    onPress={Masuk}
     >
     <Text style={{color:'red'}}>Login</Text>
     </TouchableOpacity>
      </View>
      <View style={{
        justifyContent:'center',alignItems:'center',
        marginTop:'40%'
      }}>
     <Text style={{color:'white'}}>belum punya akun ?</Text>
     <TouchableOpacity
     style={{
      justifyContent:'center',
      alignItems:'center',
      backgroundColor:'white',
     }}
     onPress={()=>navigation.navigate('Register')}
     >
     <Text style={{
      color :'#DC143C',
      fontWeight:'bold'
     }}>SIGN UP</Text>
     </TouchableOpacity>
      </View>
      </ImageBackground>
    </View>
  )
}

export default Login