import React, { useState } from 'react';

import {
  addDoc,
  collection,
} from 'firebase/firestore';
import {
  Alert,
  ImageBackground,
  StatusBar,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import { Picker } from '@react-native-picker/picker';

import { db } from '../Fb';

const Deposit = ({ navigation }) => {
  const [nama, setNama] = useState('');
  const [amount, setAmount] = useState('');
  const [bank, setBank] = useState('');
  const [kategori, setKategori] = useState('');
  const tambahDataKeFirestore = async () => {
    try {
      await addDoc(collection(db, 'deposit'), {
        nama: nama,
        amount: amount,
        bank: bank
      });
      console.log('Data berhasil ditambahkan ke Firestore.');
      navigation.navigate('OrderConfirmation', { nama, amount, bank });
      Alert.alert('done');
    } catch (error) {
      console.error('Error menambahkan data ke Firestore:', error);
    }
  };

  
  return (
    <View style={{ flex: 1 }}>
      <StatusBar style='auto' />
      <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
        <TouchableOpacity onPress={() => navigation.goBack()} style={{ marginLeft: 20, marginTop: 40 }}>
          <Text style={{ color: 'white', fontSize: 18 }}>Back</Text>
        </TouchableOpacity>
        <View style={{ justifyContent: 'center', alignItems: 'center' }}>
          <Text style={{ color: 'white', fontSize: 20, fontWeight: 'bold', marginTop: '10%' }}>Deposit</Text>
        </View>
        <View style={{ justifyContent: 'center', alignItems: 'center', marginTop: '10%' }}>
          <Picker
            selectedValue={bank}
            style={{
              height: 40,
              width: '85%',
              backgroundColor: 'white',
              marginTop: 5
            }}
            onValueChange={(itemValue) => setBank(itemValue)}
          >
            <Picker.Item label="Pilih Bank" value="" />
            <Picker.Item label="BRI" value="88810083807527692" />
            <Picker.Item label="BNI" value="8810083807527692" />
            <Picker.Item label="BCA" value="3901083807527692" />
          </Picker>
          <Text>PILIH BANK</Text>
          <TextInput
            style={{ height: 40, width: '85%', backgroundColor: 'white', borderRadius: 5, marginTop: '10%' }}
            placeholder="Nama"
            value={nama}
            onChangeText={(text) => setNama(text)}
          />
          <TextInput
            style={{ height: 40, width: '85%', backgroundColor: 'white', borderRadius: 5, marginTop: '5%' }}
            placeholder="Amount"
            value={amount}
            onChangeText={(text) => setAmount(text)}
          />
        </View>
        <View style={{
          justifyContent: 'center',
          alignItems: 'center',
          marginTop: '20%'
        }}>
          <TouchableOpacity onPress={tambahDataKeFirestore } style={{
            height: 40,
            width: 120,
            backgroundColor: 'yellow',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5
          }}>
            <Text style={{ color: 'red' }}>ORDER DEPOSIT</Text>
          </TouchableOpacity>
        </View>
      </ImageBackground>
    </View>
  );
};

export default Deposit;
