import React, {
  useEffect,
  useState,
} from 'react';

import {
  onValue,
  ref,
} from 'firebase/database';
import {
  ScrollView,
  Text,
  View,
} from 'react-native';

import {
  auth,
  dB,
} from '../Fb'; // Import database instance and auth from your Firebase setup

const Konfirmasi = () => {
    const [konfirmasiData, setKonfirmasiData] = useState(null);
    const currentUserUid = auth.currentUser ? auth.currentUser.uid : null;
  
    useEffect(() => {
      if (currentUserUid) {
        // Mengambil data konfirmasi dari Firebase Realtime Database
        const ordersRef = ref(dB, 'orders');
  
        // Menggunakan 'onValue()' untuk mendapatkan data secara realtime
        const unsubscribe = onValue(ordersRef, snapshot => {
          const data = snapshot.val();
          // data akan berisi seluruh data dari node 'orders' di Firebase
          console.log('Data dari Firebase:', data);
          // Filter data berdasarkan uid pengguna saat ini
          const userOrders = Object.values(data).filter(order => order.uid === currentUserUid);
  
          // Set data konfirmasi ke state
          setKonfirmasiData(userOrders);
        });
        console.log('currentUserUid:', currentUserUid);
        // Membersihkan listener saat komponen di-unmount
        return () => unsubscribe();
      }
    }, [currentUserUid]);
  
    return (
      <View style={{ flex: 1, backgroundColor: '#ffffff' }}>
        <View style={{ marginTop: '10%' }}>
          <ScrollView>
          <View style={{justifyContent:'center',alignItems:'center'}}>
            <Text style={{ fontWeight: 'bold', fontSize: 20, marginBottom: 10, marginLeft: '5%', color: 'green' }}>
              Konfirmasi Pesanan:
            </Text>
            </View>
            {konfirmasiData && konfirmasiData.length > 0 ? (
              konfirmasiData.map((order, index) => (
                <View key={index} style={{ marginLeft: '5%' }}>
                  <View style={{ height: 'auto', width: '95%', backgroundColor: '#F5F5DC', borderRadius: 10, elevation: 2 }}>
                    
                    <Text style={{ marginLeft:'35%',fontSize:20 }}>order barang</Text>
                    <Text>.............................................................</Text>
                    <Text style={{ fontWeight: 'bold',margin:5 }}>Tanggal: {order.tanggal}</Text>
                    <Text >----------------------------------------------------------</Text>
                    <Text style={{ fontWeight: 'bold',margin:5 }}>Total Pembayaran: Rp{order.totalPayment}</Text>
                    <Text>-----------------------------------------------------------</Text>
                    {order.cartItems && order.cartItems.length > 0 ? (
                      order.cartItems.map((item, itemIndex) => (
                        <View key={itemIndex}>
                          <Text style={{ fontWeight: 'bold' }}>
                          <View>
                            <Text style={{ color: 'green', fontWeight: 'bold' }}>{item.nama}</Text>
                            <Text>----------------------------------------------------</Text>
                           <Text> Harga: Rp{item.harga}</Text>
                           <Text>-----------------------------------------------------</Text>
                           <Text> Jumlah: {item.quantity}</Text>
                           <Text>----------------------</Text>
                           </View>
                          </Text>
                        </View>
                      ))
                    ) : (
                      <Text>Tidak ada item dalam pesanan ini.</Text>
                    )}
                    <Text>----------------------</Text>
                    <View style={{justifyContent:'center',alignItems:'center'}}>
                      <Text style={{ color: 'red', fontWeight: 'bold' }}>TERIMAKASIH </Text>
                    </View>
                  </View>
                </View>
              ))
            ) : (
              <Text>Tidak ada data konfirmasi pesanan.</Text>
            )}
          </ScrollView>
        </View>
      </View>
    );
  };
  
  export default Konfirmasi;
  
  