import React, {
  useEffect,
  useState,
} from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  get,
  ref,
  set,
} from 'firebase/database';
import {
  Image,
  ImageBackground,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  auth,
  dB,
} from '../Fb';
import { useImageURL } from '../ImageUri';

const Profile = ({navigation}) => {
  const Gambar = useImageURL();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [nama,setNama]=useState('');
  const [alamat, setAlamat] = useState('');
  const [nomorTelepon, setNomorTelepon] = useState('');
  const [password, setPassword] = useState('');
  const [bonus, setBonus] = useState('');
  const [confirm, setConfirm] = useState('');
  const [saldo, setSaldo] = useState('');
  const [foto, setFoto] = useState('');

  useEffect(() => {
    // Ambil data pengguna dari Firebase Realtime Database berdasarkan UID pengguna saat ini
    const userId = auth.currentUser.uid;
    const userRef = ref(dB, 'users/' + userId);

    get(userRef)
      .then((snapshot) => {
        const userData = snapshot.val();
        if (userData) {
          // Mengisi nilai-nilai berdasarkan data pengguna yang ada
          setName(userData.username);
          setEmail(userData.email);
          setImageUrl(userData.imageUrl);
          setAlamat(userData.alamat);
          setNomorTelepon(userData.nomorTelepon);
          // ... isi nilai-nilai lainnya ...
        }
      })
      .catch((error) => {
        console.error('Gagal mengambil data pengguna:', error);
      });
  }, []);

  const saveProfileImage = () => {
    // Mendapatkan UID pengguna saat ini
    const userId = auth.currentUser.uid;

    // Mendapatkan referensi ke data pengguna di Firebase Realtime Database
    const userRef = ref(dB, 'users/' + userId);

    // Mengganti nilai imageUrl dengan yang baru
    set(ref(dB, 'users/' + userId), {
      username :nama,
      email: email,
      alamat: alamat,
      nomorTelepon: nomorTelepon,
      
      imageUrl:imageUrl// atau imageUrl yang Anda gunakan untuk menyimpan URL gambar profil
    })
      .then(() => {
        console.log('ImageUrl berhasil diperbarui.');
        Alert.alert('upload done.');
      })
      .catch((error) => {
        console.error('Gagal memperbarui imageUrl:', error);
        Alert.alert('erorr.');
      });
    }    


  return (
    <View style={{
      flex:1
    }}>
    <StatusBar style="auto" />
      <ImageBackground   source={require('../assets/baby.jpg')}
      style={{
        flex: 1
      }}>
            <View style={{
        justifyContent:'center',alignItems:'center'
        ,marginTop:'10%'
      }}>
      {imageUrl ? (
        <Image
          source={{ uri: imageUrl }}
          style={{
            height: 120,
            width: 120,
            borderRadius: 40,
          }}
        />
      ) : (
        <Image source={require('../assets/miner.jpg')} style={{
          height: 120,
          width: 120,
          borderRadius: 40,
        }}/>
      )}
     
      <Text style={{color:'white',
    fontSize:12,fontWeight:'bold',marginTop:'10%'}}>{name}</Text>
      
     
      </View>
      <View style={{
        justifyContent:'center',alignItems:'center',
        
      }}>
     
      <Text style={{
        margin:10
      }}></Text>
      <Text style={{
        margin:10
      }}></Text>
      <TouchableOpacity
       style={{
        height:40,width:120,backgroundColor:'yellow',
        justifyContent:'center',
        alignItems:'center',
        elevation:5,
        borderRadius:3,
        margin:5
       }}
       onPress={()=>navigation.navigate('Edit')}
       >
      <Text
      style={{
        color:'blue'
      }}
      >Edit Profile</Text>
      </TouchableOpacity>
     
     
      </View>
      <View style={{
        marginTop:'10%',
        marginLeft:'5%'
      }

      }>
     <Text style={{
      color:'white',fontSize:15,fontWeight:'bold'
     }}>NFT Collection</Text>
      </View>
      <View style={{justifyContent:'center',alignItems:'center',flexDirection:'row'}}>
      <View style={{height:180,width:180,
     borderRadius:10,
    margin:10,elevation:10}}>
      </View>
      <View style={{height:180,width:180,
       borderRadius:10,
      margin:10,elevation:10}}>
        </View>
      </View>
      </ImageBackground>
    </View>
  )
}

export default Profile