import React, { useState } from 'react';

import { StatusBar } from 'expo-status-bar';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import {
  ref,
  set,
} from 'firebase/database';
import {
  Image,
  ImageBackground,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  auth,
  dB,
} from '../Fb';

const Register = ({navigation}) => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [alamat, setAlamat] = useState('');
  const [nomorTelepon, setNomorTelepon] = useState('');
  const [password, setPassword] = useState('');
  const [bonus, setBonus] = useState('');
  const [username,setUserName]=useState('')
  const [confirm, setConfirm] = useState('');
  const [saldo,setSaldo]=useState('');
  const [engine,setEngine ]=useState('');
  const [time,setTime]=useState('');
  const [position,setPosition]=useState('');
  const [penghasilan,setPenghasilan]=useState('');

 

  
  async function writeUserData() {
    const userCredential = await createUserWithEmailAndPassword(auth, email, password);
    const user = userCredential.user;
    const userId = user.uid;
    
  
    // Tambahkan jeda waktu sebelum menyimpan data ke database
    setTimeout(() => {
      try {
        const userId = auth.currentUser.uid;
        set(ref(dB, 'users/' + userId), {
          username: name,
          email: email,
           alamat: alamat,
           nomorTelepon: nomorTelepon,
           saldo: saldo,
           bonus: bonus,
           engine:engine,
           time:time,
          position:position,
          penghasilan:penghasilan,
          imageUrl:imageUrl
           // Pastikan Anda menggunakan nama properti yang benar
        })
          .then(() => {
            console.log('Data berhasil ditulis ke Firebase Realtime Database.');
            navigation.navigate('main');
          })
          .catch((error) => {
            console.error('Gagal menulis data ke Firebase Realtime Database:', error);
          });
      } catch (error) {
        console.error('Gagal mendaftar pengguna:', error);
      }
    }, 5000); // Jeda waktu dalam milidetik (1 detik)
  }



  return (
    <View style={{
      flex:1
    }}>
    <StatusBar style="auto" />
    <ImageBackground source={require('../assets/baby.jpg')}
    style={{
      flex:1
    }}
    >
     
      <View style={{
        marginTop:'30%',
        justifyContent:'center',
        alignItems:'center',
        
       }}>
       <Image source={require('../assets/miner.jpg')}
       style={{
        height:180,width:180,borderRadius:80,
        
       }}/>
       </View>
      <View style={{
        justifyContent:'center',
        alignItems:'center',
        marginTop:'10%'
      }}>
      <TextInput style={{
          height:40,
          width:'85%',
          borderWidth:0.8,
          borderColor:'white',
          borderRadius:5,
          marginTop:'5%',
          elevation:10,
          color:'white'
      }}
      placeholder='  email'
      placeholderTextColor='white'
      value={email}
      onChangeText={(text)=>setEmail(text)}
      />
      <TextInput   style={{
        height:40,
        width:'85%',
        borderWidth:0.8,
        borderColor:'white',
        borderRadius:5,
        marginTop:'5%',
        elevation:5,
        color:'white',

    }}
    placeholder= '  password'
    placeholderTextColor='white'
    secureTextEntry={true}
    />
    <TextInput   style={{
      height:40,
      width:'85%',
      borderWidth:0.8,
      borderColor:'white',
      borderRadius:5,
      marginTop:'5%',
      elevation:5,
      color: 'white'
  }}
  placeholder= '  confirm password'
  placeholderTextColor='white'
  onChangeText={(text)=>setPassword(text)}
  value={password}
  secureTextEntry={true}
  />
     <TouchableOpacity
     style={{
      justifyContent:'center',alignItems:'center',
      height:40,width:'60%',
      backgroundColor:'#00FFFF',
      borderRadius:10,
      marginTop:'5%',
      margin:5,
      elevation:10
    }}
    onPress={writeUserData}
     >
     
     <Text style={{
      color: 'red',
      fontSize:18,
      fontWeight:'bold'

     }}>SIGN UP</Text>
     </TouchableOpacity>
      </View>
      
      </ImageBackground>
    </View>
  )
}

export default Register