import {
  useEffect,
  useState,
} from 'react';

import * as ImagePicker from 'expo-image-picker';
import { StatusBar } from 'expo-status-bar';
import {
  get,
  ref as dBref,
  set,
} from 'firebase/database';
import {
  getStorage,
  ref as storageRef,
  uploadBytes,
} from 'firebase/storage';
import {
  Alert,
  Image,
  ImageBackground,
  ScrollView,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  auth,
  dB,
  getDownloadURL,
  ref as dbRef,
} from '../Fb';

export default function Edit() {
      const [imageURI, setImageURI] = useState(null);
      const [simpan, setSimpan] = useState('');
      const [profile,setProfile]=useState('');
    
      const [name, setName] = useState('');
      const [email, setEmail] = useState('');
      const [imageUrl, setImageUrl] = useState('');
      const [alamat, setAlamat] = useState('');
      const [nomorTelepon, setNomorTelepon] = useState('');
      const [password, setPassword] = useState('');
      const [bonus, setBonus] = useState('');
      const [confirm, setConfirm] = useState('');
      const [saldo, setSaldo] = useState('');
      const [foto, setFoto] = useState('');
    
      const [isDepositSuccess, setIsDepositSuccess] = useState(false);
    
      useEffect(() => {
        // Ambil data pengguna dari Firebase Realtime Database berdasarkan UID pengguna saat ini
        const userId = auth.currentUser.uid;
        const userRef = dBref(dB, 'users/' + userId);
    
        get(userRef)
          .then((snapshot) => {
            const userData = snapshot.val();
            if (userData) {
              // Mengisi nilai-nilai berdasarkan data pengguna yang ada
              setName(userData.username);
              setEmail(userData.email);
              setImageUrl(userData.imageUrl);
              setAlamat(userData.alamat);
              setNomorTelepon(userData.nomorTelepon);
              // ... isi nilai-nilai lainnya ...
            }
          })
          .catch((error) => {
            console.error('Gagal mengambil data pengguna:', error);
          });
      }, []);
    
    
    
    
    
    
    
      const pickImage = async () => {
        try {
          const permissionResult = await ImagePicker.requestMediaLibraryPermissionsAsync();
    
          if (!permissionResult.granted) {
            Alert.alert('Izin akses ke galeri diperlukan!');
            return;
          }
    
          const result = await ImagePicker.launchImageLibraryAsync({
            mediaTypes: ImagePicker.MediaTypeOptions.Images,
            allowsEditing: true,
            aspect: [1, 1],
            quality: 1,
          });
    
          if (!result.canceled) {
            const { assets } = result;
    
            if (assets && assets.length > 0) {
              const selectedImage = assets[0];
              const { uri } = selectedImage;
    
              const fileName = uri.split('/').pop();
              const folderName = 'profiles';
    
              const storage = getStorage();
              const imageRef = storageRef(storage, `${folderName}/${fileName}`);
    
              const response = await fetch(uri);
              const blob = await response.blob();
    
              await uploadBytes(imageRef, blob);
    
              const downloadURL = await getDownloadURL(imageRef);
             setImageURI(downloadURL);
              setSimpan(downloadURL);
    
            
              
               
                 
            }
          }
        } catch (error) {
          console.error('Error picking or uploading image:', error);
        }
      };
    
    
      const saveProfileImage = () => {
        // Mendapatkan UID pengguna saat ini
        const userId = auth.currentUser.uid;
    
        // Mendapatkan referensi ke data pengguna di Firebase Realtime Database
        const userRef = dBref(dB, 'users/' + userId);
    
        // Mengganti nilai imageUrl dengan yang baru
        set(dbRef(dB, 'users/' + userId), {
          username :name,
          email: email,
          alamat: alamat,
          nomorTelepon: nomorTelepon,
          
          imageUrl:simpan// atau imageUrl yang Anda gunakan untuk menyimpan URL gambar profil
        })
          .then(() => {
            console.log('ImageUrl berhasil diperbarui.');
            Alert.alert('done.');
          })
          .catch((error) => {
            console.error('Gagal memperbarui imageUrl:', error);
            Alert.alert('error.');
          });
        }  
      
    
    
      return (
        <View style={{flex:1}}>
        <StatusBar style="auto" />
        <ImageBackground   source={require('../assets/baby.jpg')}
        style={{
          flex:1
        }}>
        <ScrollView>
        <View style={{
          justifyContent:'center',
          alignItems:'center',
          marginTop:'20%'
        }}>
        {imageUrl ? (
          <Image
            source={{ uri: imageUrl }}
            style={{
              height: 120,
              width: 120,
              borderRadius: 40,
            }}
          />
        ) : (
          <Image source={require('../assets/miner.jpg')} style={{
            height: 120,
            width: 120,
            borderRadius: 40,
          }}/>
        )}
       
  
         
          <TouchableOpacity style={{
            marginTop:'5%',justifyContent:'center',
            alignItems:'center',
            borderRadius:5,
            height:40,
            width:120,
            backgroundColor:'#00FFFF'
          }}
          onPress={pickImage}
          >
          <Text style={{color:'white',fontWeight:'bold'}}>Upload Image</Text>
          </TouchableOpacity>
          </View>
          <View>
            <Image source={{ uri: imageURI }} />
          </View>
          <View style={{
            justifyContent:'center',
            alignItems:'center',
            marginTop:'5%'
          }}>
          <TextInput
          style={{
            height:40,
            width:'65%',backgroundColor:'white',
            margin:5, borderRadius:5
          }}
          placeholder='name'
          value={name}
          onChangeText={(text)=>setName(text)}
          />
          <TextInput
          style={{
            height:40,
            width:'70%',backgroundColor:'white',
            margin:5, borderRadius:5
          }}
          placeholder='phone Number'
          value={nomorTelepon}
          onChangeText={(text)=>setNomorTelepon(text)}
          />
          <TextInput
          style={{
            height:100,
            width:'85%',backgroundColor:'white',
            margin:5,
            borderRadius:5,
            
          }}
          placeholder='address'
          value={alamat}
          onChangeText={(text)=>setAlamat(text)}
          textAlignVertical='top'
          multiline={true}
          />
        
           
          </View>
          <View style={{
            justifyContent: 'center',
            alignItems: 'center',
          }}>
          <View>
            <TouchableOpacity
              onPress={saveProfileImage}
              style={{
                height:40,
                width:120,
                backgroundColor:'yellow',
                elevation:10,
                justifyContent:'center',
                alignItems:'center',
                borderRadius:5
              }}
            >
              <Text style={{color:'red'}}>Change</Text>
            </TouchableOpacity>
            </View>
          </View>
          </ScrollView>
          </ImageBackground>
        </View>
      );
    }
    
    // {imageURI && <Image source={{ uri: imageURI }} style={{ width: 200, height: 200 }} />}