import React from 'react';

import MaterialCommunityIcons
  from 'react-native-vector-icons/MaterialCommunityIcons';

import {
  createMaterialBottomTabNavigator,
} from '@react-navigation/material-bottom-tabs';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

import { AuthProvider } from './AuthContext';
import {
  AuthenticatedUserProvider,
  CartProvider,
} from './CartContext';
import Edit from './Component/Edit';
import {
  Chat,
  Deposit,
  Home,
  Keranjang,
  Konfirmasi,
  Miner,
  Profile,
  ShopingCartIcon,
  Withdraw,
} from './Component/index';
import Market from './Component/Market';
import OrderConfirmation from './Component/OrderConfirmation';
import OrderWitdraw from './Component/OrderWitdraw';
import { ImageURLProvider } from './ImageUri';
import Atul from './Screen/Atul';
import {
  Login,
  Register,
  SplashScreen,
} from './Screen/index';

const Stack = createStackNavigator();

const Tab = createMaterialBottomTabNavigator();

function MyTabs() {
  return (
    <Tab.Navigator
      initialRouteName="Feed"
      activeColor="#00ffff"
      barStyle={{ backgroundColor: '#057D05' }}
    >
      <Tab.Screen
        name="Home"
        component={Home}
        options={{
          tabBarLabel: 'Home',
          tabBarIcon: ({ color }) => (
            <MaterialCommunityIcons name="home" color={color} size={26} />
          ),
        }}
      />
      <Tab.Screen
        name="Profile"
        component={Profile}
        options={{
          tabBarLabel: 'profile',
          tabBarIcon: ({ color }) => (
            <MaterialCommunityIcons name="account" color={color} size={26} />
          ),
        }}
      />
       <Tab.Screen
        name="Miner"
        component={Miner}
        
        options={{
          tabBarLabel: 'Miner',
          tabBarIcon: ({ color }) => (
            <MaterialCommunityIcons name="alien-outline" color={color} size={30} />
            
          ),
        }}
        
      />
      <Tab.Screen
        name="Market"
        component={Market}
        options={{
          tabBarLabel: 'Market',
          tabBarIcon: ({ color }) => (
            <MaterialCommunityIcons name="wallet" color={color} size={26} />
          ),
        }}
      />
      <Tab.Screen
        name="Chat"
        component={Chat}
        options={{
          tabBarLabel: 'Chats',
          tabBarIcon: ({ color }) => (
            <MaterialCommunityIcons name="chat" color={color} size={26} />
          ),
        }}
      />
    </Tab.Navigator>
  );
}

function MyStack() {
  return (
    <Stack.Navigator>
    <Stack.Screen name="SplashScreen" component={SplashScreen} options={{headerShown: false}} />
      <Stack.Screen name="Home" component={Home}   options={{headerShown: false}}/>
      <Stack.Screen name="Miner" component={Miner}  options={{headerShown: false}} />
      <Stack.Screen name="Deposit" component={Deposit}  options={{headerShown: false}}/>
      <Stack.Screen name="Profile" component={Profile} options={{headerShown: false}} />
      <Stack.Screen name="Chat" component={Chat}  options={{headerShown: false}}/>
      <Stack.Screen name="Login" component={Login}  options={{headerShown: false}}/>
      <Stack.Screen name="Register" component={Register}  options={{headerShown: false}} />
      <Stack.Screen name="main" component={MyTabs}  options={{headerShown: false}} />
      <Stack.Screen name="Konfirmasi" component={Konfirmasi}  options={{headerShown: false}} />
      <Stack.Screen name="Keranjang" component={Keranjang}  options={{headerShown: false}} />
      <Stack.Screen name="ShopingCartIcon" component={ShopingCartIcon}  options={{headerShown: false}} />
      <Stack.Screen name="market" component={Market}  options={{headerShown: false}} />
      <Stack.Screen name="OrderConfirmation" component={OrderConfirmation}  options={{headerShown: false}} />
      <Stack.Screen name="OrderWithdraw" component={OrderWitdraw}  options={{headerShown: false}} />
      <Stack.Screen name="Withdraw" component={Withdraw}  options={{headerShown: false}} />
      <Stack.Screen name="Edit" component={Edit}  options={{headerShown: false}} />
      <Stack.Screen name="atul" component={Atul}  options={{headerShown: false}} />

    </Stack.Navigator>
  );
}





const App = () => {
  return (
    <NavigationContainer>
    
      
      <CartProvider>
      <AuthenticatedUserProvider>
      <ImageURLProvider> 
          <AuthProvider>
            <MyStack />
          </AuthProvider>
        </ImageURLProvider>
        </AuthenticatedUserProvider>
      </CartProvider>
      
   
  </NavigationContainer>
  )
}

export default App