import React, { useState } from 'react';

import {
  addDoc,
  collection,
} from 'firebase/firestore';
import {
  Alert,
  ImageBackground,
  StatusBar,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import { db } from '../Fb';

const Withdraw = ({ navigation }) => {
  const [nama, setNama] = useState('');
  const [amount, setAmount] = useState('');
  const [bank,setBank] = useState('');

  const tambahDataKeFirestore = async () => {
    try {
      await addDoc(collection(db, 'withdraw'), {
        nama: nama,
        amount: amount,
        bank: bank
      });
      console.log('Data berhasil ditambahkan ke Firestore.');
      navigation.navigate('OrderWithdraw', { nama, amount, bank });

      Alert.alert('done');
    } catch (error) {
      console.error('Error menambahkan data ke Firestore:', error);
    }
  };

 
  return (
    <View style={{ flex: 1 }}>
      <StatusBar style='auto' />
      <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
        <TouchableOpacity onPress={() => navigation.goBack()} style={{ marginLeft: 20, marginTop: 40 }}>
          <Text style={{ color: 'white', fontSize: 18 }}>Back</Text>
        </TouchableOpacity>
        <View style={{ justifyContent: 'center', alignItems: 'center' }}>
          <Text style={{ color: 'white', fontSize: 20, fontWeight: 'bold', marginTop: '10%' }}>Withdraw</Text>
        </View>
        <View style={{ justifyContent: 'center', alignItems: 'center', marginTop: '10%' }}>
          <TextInput
            style={{ height: 40, width: '85%', backgroundColor: 'white', borderRadius: 5 }}
            placeholder="Nama"
            value={nama}
            onChangeText={(text) => setNama(text)}
          />
          <TextInput
            style={{ height: 40, width: '85%', backgroundColor: 'white', borderRadius: 5, marginTop: '5%' }}
            placeholder="Amount"
            value={bank}
            onChangeText={(text) => setBank(text)}
          />
          <TextInput
          style={{ height: 40, width: '85%', backgroundColor: 'white', borderRadius: 5, marginTop: '5%' }}
          placeholder="no rekening anda"
          value={amount}
          onChangeText={(text) => setAmount(text)}
        />

        <TouchableOpacity
        onPress={tambahDataKeFirestore}
        style={{
          height:40,
          width:120,
          backgroundColor:'yellow',
          borderRadius:5,
          justifyContent:'center',
          alignItems:'center',
          marginTop:'20%'
        }}
        >
        <Text style={{
          color:'red'
        }}>Order Withdraw</Text>
        </TouchableOpacity>
         
        </View>
      </ImageBackground>
    </View>
  );
};

export default Withdraw;
