import React, {
  useEffect,
  useState,
} from 'react';

import * as ImagePicker from 'expo-image-picker';
import { StatusBar } from 'expo-status-bar';
import {
  addDoc,
  collection,
  getDocs,
} from 'firebase/firestore';
import {
  getStorage,
  ref as storageRef,
  uploadBytes,
} from 'firebase/storage';
import {
  Alert,
  Button,
  Image,
  ScrollView,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  db,
  getDownloadURL,
} from '../Fb';

//88810083807527692
  const Atul = ({navigation}) => {
    const [barang, setBarang] = useState('');
    const [jumlah, setJumlah] = useState('');
    const [harga, setHarga] = useState('');
    const [imageUrl,setImageUrl]=useState('');
    const [dataFromFirestore, setDataFromFirestore] = useState([]);
    const [imageURI, setImageURI] = useState(null);
    const [simpan,setSimpan]=useState('');
    const [waktu,setWaktu]=useState('');
    const [nama,setNama]=useState('');
    const [deskripsi,setDeskripsi]=useState('');
    const [dayaKomputasi,setDayaKomputasi]=useState('');
   
    const [penghasilanHarian,setPenghasilanHarian]=useState('');
    const [totalPendapatan,setTotalPendapatan]=useState('');
  
  
   
    const pickImage = async () => {
      try {
        const permissionResult = await ImagePicker.requestMediaLibraryPermissionsAsync();
  
        if (!permissionResult.granted) {
          Alert.alert('Izin akses ke galeri diperlukan!');
          return;
        }
  
        const result = await ImagePicker.launchImageLibraryAsync({
          mediaTypes: ImagePicker.MediaTypeOptions.Images,
          allowsEditing: true,
          aspect: [1, 1],
          quality: 1,
        });
  
        if (!result.canceled) {
          const { assets } = result;
  
          if (assets && assets.length > 0) {
            const selectedImage = assets[0];
            const { uri } = selectedImage;
  
            const fileName = uri.split('/').pop();
            const folderName = 'Product';
  
            const storage = getStorage();
            const imageRef = storageRef(storage, `${folderName}/${fileName}`);
  
            const response = await fetch(uri);
            const blob = await response.blob();
  
            await uploadBytes(imageRef, blob);
  
            const downloadURL = await getDownloadURL(imageRef);
           setImageURI(downloadURL);
            setSimpan(downloadURL);      
          }
        }
      } catch (error) {
        console.error('Error picking or uploading image:', error);
      }
    };
  const tambahDataKeFirestore = async () => {
      try {
        await addDoc(collection(db,'miner'), {
          barang: barang,
          jumlah: jumlah,
          harga: harga,
          imageUrl: imageURI,
          nama:nama,
          deskripsi:deskripsi,
          dayaKomputasi:dayaKomputasi,
           waktu : waktu,
          penghasilanHarian:penghasilanHarian,
          totalPendapatan:totalPendapatan,
        });
        console.log('Data berhasil ditambahkan ke Firestore.');
        Alert.alert('done')
      } catch (error) {
        console.error('Error menambahkan data ke Firestore:', error);
      }
    };
    const readDataFromFirestore = async (product) => {
      try {
        const querySnapshot = await getDocs(collection(db, 'miner'));
        const data = querySnapshot.docs.map((doc) => {
          const id = doc.id;
          return { ...doc.data(), id };
        });
        return data;
      } catch (error) {
        console.error('Error reading data from Firestore:', error);
        return [];
      }
    };
  
  
    useEffect(() => {
      const fetchData = async () => {
        const data = await readDataFromFirestore('miner');
        setDataFromFirestore(data);
     console.log(data);
      };
  
      fetchData();
    }, []);
  
    return (
      <View style={{
        flex:1
      }}>
      <StatusBar style='auto'/>
      <ScrollView>
      <View style={{justifyContent:'center',alignItems:'center',marginTop:'20%'}}>
      {imageURI && <Image source={{ uri: imageURI }} style={{ width: 200, height: 200 }} />}
      <Button title="Pilih Gambar" onPress={pickImage}/>
      </View>
      <Image source={{ uri: imageURI }} />
      <View>
      </View>
      <View style={{
        justifyContent:'center',
        alignItems:'center'
      }}>
      
        <TextInput style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='nama'
        value={nama}
        onChangeText={(text)=>setNama(text)}/>
        <TextInput style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='barang'
        value={barang}
        onChangeText={(text)=>setBarang(text)}/>
        <TextInput
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='jumlah'
        value={jumlah}
        onChangeText={(text)=>setJumlah(text)} 
        />
        <TextInput 
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='sewa'
        value={harga}
        onChangeText={(text)=>setHarga(text)}
        />
       
        <TextInput 
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='daya komputasi'
        value={dayaKomputasi}
        onChangeText={(text)=>setDayaKomputasi(text)}
        />
        <TextInput 
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='penghasilan harian'
        value={penghasilanHarian}
        onChangeText={(text)=>setPenghasilanHarian(text)}
        />
        <TextInput 
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='total pendapatan'
        value={totalPendapatan}
        onChangeText={(text)=>setTotalPendapatan(text)}
        />
        <TextInput 
        style={{
          height:40,
          width:'85%',
          backgroundColor:'white',
          marginTop:5
        }}
        placeholder='lama sewa'
        value={waktu}
        onChangeText={(text)=>setWaktu(text)}
      
        />
        <TouchableOpacity
        style={{
          height:40,width:120,backgroundColor:'#00FFFF',
          marginTop:'5%',justifyContent:'center',alignItems:'center',
          borderRadius:5,elevation:4
        }}
        onPress={tambahDataKeFirestore}
        >

        <Text style={{color:'blue'}}>Upload Product</Text>
        </TouchableOpacity>
        
        </View>
        <View>
        <TouchableOpacity
        onPress={()=>navigation.navigate('market')}>
        <Text>Ke Market</Text>
        </TouchableOpacity>
        
        </View>
        </ScrollView>
      </View>
    )
  }
  
  export default Atul