import React, {
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from 'react';

import {
  get,
  ref,
} from 'firebase/database';
import {
  addDoc,
  collection,
  deleteDoc,
  doc,
  onSnapshot,
  orderBy,
  query,
} from 'firebase/firestore';
import {
  Image,
  ImageBackground,
  ScrollView,
  StatusBar,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import { useNavigation } from '@react-navigation/native';

import {
  auth,
  db,
  dB,
} from '../Fb';
import { useImageURL } from '../ImageUri';

const Chat = () => {
  const Gambar = useImageURL();
  const [text, setText] = useState('');
  const [messages, setMessages] = useState([]);
  const navigation = useNavigation();
  const [pot, setPot] = useState('');
  const [nama, setNama] = useState('');
  const [name, setName] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [username, setUserName] = useState('');

  useEffect(() => {
    const userId = auth.currentUser.uid;
    const userRef = ref(dB, 'users/' + userId);

    get(userRef)
      .then((snapshot) => {
        const userData = snapshot.val();
        if (userData) {
          setName(userData.username);
          setImageUrl(userData.imageUrl);
        }
      })
      .catch((error) => {
        console.error('Gagal mengambil data pengguna:', error);
      });
  }, []);

  const scrollViewRef = useRef();

  useLayoutEffect(() => {
    const collectionRef = collection(db, 'chats');
    const q = query(collectionRef, orderBy('createdAt', 'desc'));

    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const reversedMessages = querySnapshot.docs.map((doc) => ({
        _id: doc.id,
        createdAt: doc.data().createdAt.toDate(),
        text: doc.data().text,
        user: doc.data().user,
        name: doc.data().name,
        imageUrl: doc.data().imageUrl,
      })).reverse();

      setMessages(reversedMessages);
      scrollViewRef.current.scrollToEnd({ animated: true });
    });

    return unsubscribe;
  }, []);

  const deleteMessage = (messageId) => {
    const messageRef = doc(db, 'chats', messageId);
    deleteDoc(messageRef)
      .then(() => {
        console.log("Pesan berhasil dihapus");
      })
      .catch((error) => {
        console.error("Error removing document: ", error);
      });
  };

  const onSend = () => {
    if (text.trim() === '' || !name) {
      return;
    }

    addDoc(collection(db, 'chats'), {
      createdAt: new Date(),
      text,
      user: auth.currentUser.email,
      name: name,
      imageUrl: imageUrl,
    })
      .then((docRef) => {
        console.log('Document written with ID: ', docRef.id);
      })
      .catch((error) => {
        console.error('Error adding document: ', error);
      });

    setText('');
  };

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />
      <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
        <View style={styles.messageContainer}  >
          <ScrollView ref={scrollViewRef} onContentSizeChange={() => scrollViewRef.current.scrollToEnd({ animated: true })}>
            {messages.map((message) => (
              <View style={{ flexDirection: 'row', alignItems: 'center' }} key={message._id}>
                <View style={{ height: 40, width: 40, borderWidth: 1, borderColor: 'white', borderRadius: 100, justifyContent: 'center', alignItems: 'center', margin: 10 }}>
                  {message.imageUrl ? (
                    <Image source={{ uri: message.imageUrl }} style={{ height: 60, width: 60, borderRadius: 40 }} />
                  ) : (
                    <Image source={require('../assets/miner.jpg')} style={{ height: 60, width: 60, borderRadius: 40 }}/>
                  )}
                </View>
                <View style={{ margin: 5 }} key={message._id}>
                  <View style={{ height:'auto', width: '100%' }}>
                    <Text style={{ color: 'yellow', margin: 5 }} key={message._id}>{message.name}</Text>
                  </View>
                  <View style={{ height: 'auto', width: 'auto', backgroundColor: 'white', borderRadius: 5 }} key={message._id}>
                    <Text style={styles.messageText} key={message._id}>
                      {message.text}
                    </Text>
                    {message.user === auth.currentUser.email && (
                      <TouchableOpacity onPress={() => deleteMessage(message._id)}
                      style={{height:20,width:50,borderWidth:0.5,borderColor:'black',borderRadius:5}}>
                        <Text style={{ color: 'red' }}>Hapus</Text>
                      </TouchableOpacity>
                    )}
                  </View>
                </View>
              </View>
            ))}
          </ScrollView>
        </View>
        <View style={styles.inputContainer}>
          <TextInput style={styles.input} value={text} onChangeText={setText} placeholder="Type a message..." multiline={true} />
          <TouchableOpacity style={styles.sendButton} onPress={onSend}>
            <Text>Send</Text>
          </TouchableOpacity>
        </View>
      </ImageBackground>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#FFFFFF',
  },
  messageContainer: {
    flex: 1,
    paddingHorizontal: 10,
    paddingVertical: 5,
  },
  messageText: {
    fontSize: 12,
    marginBottom: 10,
    color: 'black',
  },
  inputContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    backgroundColor: '#FFFFFF',
    paddingHorizontal: 10,
    paddingVertical: 5,
  },
  input: {
    flex: 1,
    height: 40,
    borderWidth: 1,
    borderColor: 'gray',
    borderRadius: 20,
    paddingLeft: 10,
  },
  sendButton: {
    backgroundColor: 'lightblue',
    borderRadius: 20,
    width: 40,
    height: 40,
    justifyContent: 'center',
    alignItems: 'center',
    marginLeft: 5,
  },
});

export default Chat;
