import React, {
  useEffect,
  useState,
} from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  Image,
  ImageBackground,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  useNavigation,
} from '@react-navigation/native'; // Import useNavigation

import { useCart } from '../CartContext';
import ShopingCartIcon from '../Component/ShopingCartIcon';
import {
  collection,
  db,
  getDocs,
} from '../Fb';

const Market = () => {
    const [dataFromFirestore, setDataFromFirestore] = useState([]);
    const { cartItems, dispatch } = useCart();
    const navigation = useNavigation(); // Initialize navigation
  
    const readDataFromFirestore = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'miner'));
        const data = querySnapshot.docs.map((doc) => {
          const id = doc.id;
          return { ...doc.data(), id };
        });
        return data;
      } catch (error) {
        console.error('Error reading data from Firestore:', error);
        return [];
      }
    };
  
    useEffect(() => {
      const fetchData = async () => {
        const data = await readDataFromFirestore();
        setDataFromFirestore(data);
      };
  
      fetchData();
    }, []);
  
    const addToCart = (product) => {
      dispatch({ type: 'ADD_TO_CART', payload: product });
    };
  
    const navigateToProductDescription = (productId) => {
      navigation.navigate('ProductDescription', { productId: productId });
    };
  
    return (
      <View style={styles.container}>

        <StatusBar style="auto" />
        <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
        <View style={{ marginTop: '10%' }}>
          <ShopingCartIcon navigation={navigation} />
        </View>
        <View style={{ marginTop: '10%', justifyContent: 'center', alignItems: 'center' }}>
          <Text style={{ fontSize: 25, fontWeight: 'bold', fontStyle: 'italic', color: '#7FFF00' }}>M- GREEN</Text>
        </View>
        <ScrollView>
          {dataFromFirestore.map((data, index) => (
            <TouchableOpacity key={index} onPress={() => navigateToProductDescription(data.id)}>
              <View style={styles.productContainer}>
                <View style={styles.imageContainer}>
                  <Image source={{ uri: data.imageUrl }} style={styles.productImage} />
                </View>
                <View style={styles.productDetails}>
                <View style={{height:40,width:80,elevation:5,borderRadius:5,justifyContent:'center',alignItems:'center'}}>
                  <Text style={styles.productName}>{data.nama}</Text>
                  </View>
                  <View style={{height:30,width:129,elevation:5,borderRadius:5,justifyContent:'center',alignItems:'center'}}>
                  <Text style={styles.productName}>{`sewa :$ ${ data.harga}`}</Text>
                  </View>
                  <Text style={styles.productPrice}>{`Stock         ${data.jumlah}`}</Text>
                  <Text style={styles.oke}>{`waktu Beralaku        :    ${data.waktu}`} Hari</Text>
                  <Text style={styles.oke}>{`daya Komputasi      :   ${data.dayaKomputasi}`}TH</Text>
                  <Text style={styles.oke}>{`penghasilanHarian  :   $${data.penghasilanHarian}`}</Text>
                  <Text style={styles.oke}>{`totalPendapatan      :   ${data.totalPendapatan}`}</Text>
                  <TouchableOpacity style={styles.addToCartButton} onPress={() => addToCart(data)}>
                    <Text style={styles.addToCartButtonText}>SEWA</Text>
                  </TouchableOpacity>
                </View>
              </View>
            </TouchableOpacity>
          ))}
        </ScrollView>
        </ImageBackground>
      </View>
    );
  };
  
  const styles = StyleSheet.create({
    container: {
      flex: 1,
      backgroundColor: '#FFF5EE',
    },
    productContainer: {
      justifyContent: 'center',
      alignItems: 'center',
      marginTop: 20,
      flexDirection: 'row',
    },
    imageContainer: {
      height: 140,
      width: 180,
      backgroundColor: 'white',
      margin: 10,
      borderRadius: 5,
      elevation: 5,
    },
    productImage: {
      height: 120,
      width: 180,
      resizeMode: 'cover',
    },
    productDetails: {
      flex: 1,
      margin: 10,
    },
    productName: {
      fontWeight: 'bold',
      fontSize: 20,
      color:'white'
    },
    productPrice: {
      fontWeight: 'bold',
      fontSize: 10,
      color:'white'
    },
    addToCartButton: {
      height: 40,
      width: 140,
      backgroundColor: '#00FFFF',
      borderRadius: 5,
      justifyContent: 'center',
      alignItems: 'center',
      marginTop: 10,
    },
    addToCartButtonText: {
      color: '#7FFF00',
      fontWeight: 'bold',

    },
    oke :{
      color:'white'
    },
  });
  
  export default Market;
  