import React, {
  useEffect,
  useRef,
} from 'react';

import { Video } from 'expo-av';
import { StatusBar } from 'expo-status-bar';
import {
  Animated,
  Dimensions,
  Image,
  ImageBackground,
  ScrollView,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

import { useCart } from '../CartContext';
import {
  collection,
  db,
  getDocs,
} from '../Fb';

const RunningAd = ({ runningText }) => {
  const adRef = useRef(new Animated.Value(0)).current;

  const startAnimation = () => {
    Animated.loop(
      Animated.timing(adRef, {
        toValue: 1,
        duration: 6000,
        useNativeDriver: true,
      })
    ).start();
  };

  useEffect(() => {
    startAnimation();
  }, []);
  return (
    <Animated.View
      style={{
        transform: [
          {
            translateX: adRef.interpolate({
              inputRange: [0, 1],
              outputRange: [Dimensions.get('window').width, -Dimensions.get('window').width],
            }),
          },
        ],
      }}
    >
      <Text style={{ fontWeight: 'bold', fontSize: 20, fontStyle: 'italic', color: 'white' }}>{runningText}</Text>
    </Animated.View>
  );
};




const Home = ({navigation}) => {
  const [dataFromFirestore, setDataFromFirestore] = React.useState([]);
  const [runningText, setRunningText] = React.useState('');
  const { cartItems, dispatch } = useCart();
  const [dapatgambar, setDapatgambar] = React.useState(null);
  const [adData, setAdData] = React.useState([]);
  const [currentAdIndex, setCurrentAdIndex] = React.useState(0);

  const readDataFromFirestore = async (product) => {
    try {
      const querySnapshot = await getDocs(collection(db, product));
      const data = querySnapshot.docs.map((doc) => {
        const id = doc.id;
        return { ...doc.data(), id };
      });
      return data;
    } catch (error) {
      console.error('Error reading data from Firestore:', error);
      return [];
    }
  };


  const fetchRunningText = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'iklantext'));
      const data = querySnapshot.docs.map((doc) => doc.data());
      if (data.length > 0) {
        setRunningText(data[0].text);
      }
    } catch (error) {
      console.error('Error reading running text from Firestore:', error);
      setRunningText('');
    }
  };

  const fetchAdVideo = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'iklan'));
      const adData = querySnapshot.docs.map((doc) => doc.data());
      setAdData(adData);
      if (adData.length > 0) {
        setDapatgambar(adData[0].videoUrl);
        
      }
    } catch (error) {
      console.error('Error reading ad video from Firestore:', error);
      setDapatgambar(null);
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      const data = await readDataFromFirestore('sayuran');
      setDataFromFirestore(data);
    };

    fetchData();
    fetchRunningText();
    fetchAdVideo();
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentAdIndex((prevIndex) => (prevIndex + 1) % adData.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [adData]);



  return (
    <View style={{
      flex: 1,
    }}>
    <StatusBar style="auto" />
      <ImageBackground source={require('../assets/baby.jpg')}
        style={{
          flex: 1
        }}
      >
   <ScrollView>

        <View
          style={{
            justifyContent: 'space-between',
            alignItems: 'center',
            flexDirection: 'row',
            marginTop: '5%'
          }}
        >

          <View
            style={{
              flex: 1,
              marginLeft: '5%'
            }}

          >
            <Text style={{ color: 'white', fontSize: 25, fontWeight: 'bold', fontStyle: 'italic' }}>BTC-Land
            </Text>
          </View>
          <View style={{
            flex: 1, justifyContent: 'flex-end',
            alignItems: 'center', marginLeft: '30%'
          }}>
            <Image source={require('../assets/miner.jpg')}
              style={{
                height: 60,
                width: 60
              }}
            />
          </View>
        </View>
        <View style={{
          justifyContent:'center',
          alignItems:'center',
          marginTop:'5%'
        }}>
        <View style={{
          height:180,
          width:'93%',
          borderRadius:5,
          elevation:10,
        }}>
        {dapatgambar && (
          <Video
            source={{ uri: dapatgambar }}
            rate={1.0}
            volume={1.0}
            isMuted={false}
            resizeMode="cover"
            shouldPlay
            isLooping
            style={{
              height: 180, width: '100%', borderRadius: 10, elevation: 5
              , borderColor: 'red', borderWidth: 2,
            }}
          />
        )}
        </View>
        </View>
        <View style={{
          height:40,
          width:'100%',
          elevation:10,
          marginTop:'5%',
          justifyContent:'center',
          alignItems:'flex-start'
        }}>
        <RunningAd runningText={runningText} />
        </View>
        <View style={{
          marginTop:'5%'
        }}>
        <Text style={{color:'white',fontSize:20,fontWeight:'bold',}}>Product</Text>
        </View>
        <View style={{
          justifyContent:'center',
          alignItems:'center',
          flexDirection:'row'
        }}>
        <TouchableOpacity style={{
          height:120,width:180,
          elevation:5,
          margin:10,
          borderRadius:5,
          justifyContent:'center',alignItems:'center'
        }}
        onPress={()=>navigation.navigate('Deposit')}
        >
        
        <Image source={require('../assets/gretvenwallet.png')}
        style={{
          height:80,width:80
        }}/>
        <Text style={{
          color:'white',
          fontWeight:'bold'
        }}>DEPOSIT</Text>
        </TouchableOpacity>
        <TouchableOpacity style={{
          height:120,width:180,
         elevation:5,
          margin:10,
          borderRadius:5,
          justifyContent:'center',alignItems:'center'
        }}
      
        >
        <Image source={require('../assets/market.png')}
        style={{
          height:80,width:80
        }}/>
        
        <Text style={{
          color:'white',
          fontWeight:'bold'
        }}> SEWA DAN BELI</Text>
        </TouchableOpacity>
        </View>
        <View
        style={{
          justifyContent:'center',
          alignItems:'center',
          flexDirection:'row'
        }} 
        >
        <TouchableOpacity
        style={{
          height:100,width:180,
         elevation:5,
          margin:10,
          borderRadius:5,
          justifyContent:'center',alignItems:'center'
        }}
       
        >
        <Image source={require('../assets/news.jpg')}
        style={{
          height:60,width:70,
          borderRadius:2
        }}/>
        
        <Text style={{
          color:'white',
          fontWeight:'bold'
        }}>UPDATE</Text>
        </TouchableOpacity>
        <TouchableOpacity
        style={{
          height:100,width:180,
         elevation:5,
          margin:10,
          borderRadius:10,
          justifyContent:'center',alignItems:'center'
        }}
        onPress={()=>navigation.navigate('Withdraw')}
        >
        <Image source={require('../assets/treding.png')}
        style={{
          height:60,width:70,
          borderRadius:2
        }}/>
        
        <Text style={{
          color:'white',
          fontWeight:'bold'
        }}>WITHDRAW</Text>
        </TouchableOpacity>
        </View>
        </ScrollView>
      </ImageBackground>
    </View>
  )
}

export default Home
