import React, {
  useEffect,
  useState,
} from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  get,
  ref,
} from 'firebase/database';
import {
  ImageBackground,
  Text,
  View,
} from 'react-native';

import {
  auth,
  dB,
} from '../Fb';

const Miner = () => {

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  
  
  const [bonus, setBonus] = useState('');
  const [username,setUserName]=useState('')
 
  const [saldo,setSaldo]=useState('');
  const [engine,setEngine ]=useState('');
  const [time,setTime]=useState('');
  const [position,setPosition]=useState('');
  const [penghasilan,setPenghasilan]=useState('');


  useEffect(() => {
    // Ambil data pengguna dari Firebase Realtime Database berdasarkan UID pengguna saat ini
    const userId = auth.currentUser.uid;
    const userRef = ref(dB, 'users/' + userId);

    get(userRef)
      .then((snapshot) => {
        const userData = snapshot.val();
        if (userData) {
          // Mengisi nilai-nilai berdasarkan data pengguna yang ada
          setName(userData.username);
          setEmail(userData.email);
          setImageUrl(userData.imageUrl);
          setPenghasilan(userData.penghasilan);
          setSaldo(userData.saldo);
          setEngine(userData.engine);
          setPosition(userData.position);
          setTime(userData.time);
          
          // ... isi nilai-nilai lainnya ...
        }
      })
      .catch((error) => {
        console.error('Gagal mengambil data pengguna:', error);
      });
  }, []);


  return (
    <View style={{
      flex:1,
    }}>
    <StatusBar style='auto'/>
    <ImageBackground   source={require('../assets/baby.jpg')}
    style={{flex:1}}>
    <View style={{
      justifyContent:'center',alignItems:'center',
      marginTop:'20%'
    }}>
      <Text style={{color:'white',fontSize:20}}>Miner</Text>
      </View>
      <View style={{
        
        justifyContent:'center',
        alignItems:'center'
      }}>
      <View style={{
        height:40,
        width:'85%',
        elevation:10,
        borderRadius:5,
        justifyContent:'center',
        alignItems:'center',
        marginTop:'5%'
      }}>
      
      <Text style={{
        color:'white'
      }}>ENGINE <Text style={{fontSize:20,fontWeight:'bold'}}>{engine}</Text></Text>
      </View>
      <View
      style={{
        height:40,
        width:'85%',
        elevation:10,
        borderRadius:5,
        justifyContent:'center',
        alignItems:'center',
        marginTop:'5%'
      }} 
      >
      <Text style={{
        color:'white'
      }}>POSITION: <Text style={{fontSize:20,fontWeight:'bold'}}>{position}</Text>   </Text>
      </View>
      <View
      style={{
        height:40,
        width:'85%',
        elevation:10,
        borderRadius:5,
        justifyContent:'center',
        alignItems:'center',
        marginTop:'5%'
      }}
      >
      <Text style={{
        color:'white'
      }}>TIME:      <Text  style={{fontSize:20,fontWeight:'bold'}}>{time}</Text></Text>
      </View>
      <View
      style={{
        height:40,
        width:'85%',
        elevation:10,
        borderRadius:5,
        justifyContent:'center',
        alignItems:'center',
        marginTop:'5%'
      }}
      >
      <Text style={{
        color:'white'
      }}>PENGHASILAN:   <Text  style={{fontSize:20,fontWeight:'bold'}}>{penghasilan}</Text></Text>
      </View>
      </View>
      </ImageBackground>
    </View>
  )
}

export default Miner