import React, { useState } from 'react';

import {
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';

import { Ionicons } from '@expo/vector-icons';

import { useCart } from '../CartContext';

const ShopingCartIcon = ({navigation}) => {
  const { cartItems } = useCart();
  const [searchText, setSearchText] = useState(''); // State untuk menyimpan kata kunci pencarian

  const cartItemsCount = cartItems.length;

  return (
    <View style={styles.container}>
      <View style={styles.searchContainer}>
        <TextInput
          style={styles.searchInput}
          placeholder="Cari barang..."
          value={searchText}
          onChangeText={(text) => setSearchText(text)}
        />
        <Ionicons name="ios-search" size={25} color="#CD5C5C" />
      </View>
        <TouchableOpacity  onPress={()=>navigation.navigate('Keranjang')}>
      <Ionicons name="ios-cart" size={30} color="yellow" />
      {cartItemsCount > 0 && (
        <View style={styles.cartItemCount}>
     
          <Text style={styles.cartItemCountText}>{cartItemsCount}</Text>
          
        </View>
        
      )}
      </TouchableOpacity>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'absolute',
    top: 10,
    right: 20,
    flexDirection: 'row',
    alignItems: 'center',
  },
  searchContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    backgroundColor: '#f2f2f2',
    borderRadius: 5,
    padding: 3,
    width:'85%',
    marginLeft:'5%'
    
  },
  searchInput: {
    flex: 1,
    paddingHorizontal: 10,
  },
  cartItemCount: {
    position: 'absolute',
    top: -10,
    right: -10,
    backgroundColor: 'red',
    borderRadius: 10,
    width: 20,
    height: 20,
    justifyContent: 'center',
    alignItems: 'center',
  },
  cartItemCountText: {
    color: 'white',
    fontWeight: 'bold',
  },
});

export default ShopingCartIcon;
