import React, {
  useEffect,
  useState,
} from 'react';

import { StatusBar } from 'expo-status-bar';
import { ref } from 'firebase/database';
import {
  ImageBackground,
  ScrollView,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

import {
  FontAwesome,
  MaterialIcons,
} from '@expo/vector-icons';

import { useCart } from '../CartContext';
import {
  auth,
  dB,
  onValue,
  push,
} from '../Fb';

const Keranjang = ({ navigation }) => {
  const { cartItems, dispatch } = useCart();
  const [userData, setUserData] = useState(null);

  // Mendapatkan data user
  useEffect(() => {
    // Fetch user data from the database
    const userId = auth.currentUser.uid; // Assuming you have already imported `auth` from Firebase
    const userRef = ref(dB, 'users/' + userId);

    // Subscribe to the changes in the user data
    const unsubscribe = onValue(userRef, (snapshot) => {
      const data = snapshot.val();
      setUserData(data);
    });

    // Clean up the subscription when the component is unmounted
    return () => unsubscribe();
  }, []);

  if (!userData) {
    return <Text></Text>;
  }

  // Fungsi untuk menghitung total pembayaran
  const calculateTotal = () => {
    let total = 0;
    cartItems.forEach((item) => {
      let price = parseFloat(item.harga);
      let quantity = parseInt(item.quantity);
      if (!isNaN(price) && !isNaN(quantity)) {
        total += price * quantity;
      }
    });
    return total;
  };

  // Fungsi untuk menambah jumlah produk di keranjang
  const increaseQuantity = (product) => {
    dispatch({ type: 'ADD_TO_CART', payload: product });
  };

  // Fungsi untuk mengurangi jumlah produk di keranjang
  const decreaseQuantity = (product) => {
    if (product.quantity > 1) {
      dispatch({ type: 'DECREASE_QUANTITY', payload: product });
    }
  };

  // Fungsi untuk menghapus produk dari keranjang
  const removeFromCart = (product) => {
    dispatch({ type: 'REMOVE_FROM_CART', payload: product });
  };

  const handleCheckOut = () => {
    const user = auth.currentUser;

    // Pastikan pengguna masuk sebelum melanjutkan
    if (!user) {
      console.log('Harap masuk sebelum melakukan pesanan.');
      return;
    }

    // Implementasi pengiriman data ke Firebase Realtime Database

    const ordersRef = ref(dB, 'orders');

    // Buat objek data untuk pesanan
    const orderData = {
      name: userData.username,
      alamat: userData.alamat, // Sertakan alamat dari state
      email: userData.email, // Sertakan email dari state
      nomorTelepon: userData.nomorTelepon,
      uid: user.uid,
      cartItems: cartItems,
      totalPayment: calculateTotal(), // Pastikan bahwa nilai totalPayment adalah numerik dan valid
      tanggal: new Date().toISOString(),
    };

    // Kirim data pesanan ke Firebase Realtime Database dengan menggunakan metode push
    push(ordersRef, orderData)
      .then(() => {
        console.log('Data pesanan berhasil dikirim!');
        navigation.navigate('Konfirmasi');
        // Setelah data berhasil dikirim, panggil clearFromCart
        clearFromCart();
      })
      .catch((error) => {
        console.error('Error saat mengirim data pesanan:', error);
      });
  };

  const clearFromCart = () => {
    dispatch({ type: 'CLEAR_CART' });
  };

  return (
    <View style={{ flex: 1, backgroundColor: '#ffffff' }}>
    <StatusBar style='auto'/>
    <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
      <ScrollView>
        <View style={{ marginTop: '5%' }}>
          <Text style={{ fontWeight: 'bold', fontSize: 20, marginVertical: 10, color: 'orange' }}>Keranjang Belanja:</Text>
          {cartItems.map((item, index) => (
            <View key={index}>
              <View>
                <Text style={{ color: 'red', fontSize: 15, marginLeft: '2%' }}> {item.nama}</Text>
                <Text style={{ fontSize: 15, marginLeft: '2%',color:'white' }}> Harga    : ${item.harga}</Text>
                <Text style={{ fontSize: 15, marginLeft: '2%',color:'white' }}> Jumlah  : {item.quantity}</Text>
              </View>
              <View style={{ justifyContent: 'center', alignItems: 'center', flexDirection: 'row' }}>
                <View style={{ height: 30, width: 80, margin: 10 }}>
                  <TouchableOpacity onPress={() => decreaseQuantity(item)} style={{ flex: 1, justifyContent: 'center', alignItems: 'center', borderRadius: 10 }}>
                    <FontAwesome name="minus-circle" size={24} color="black" />
                  </TouchableOpacity>
                </View>
                <View style={{ height: 30, width: 80, margin: 10 }}>
                  <TouchableOpacity onPress={() => increaseQuantity(item)} style={{ flex: 1, justifyContent: 'center', alignItems: 'center', borderRadius: 10 }}>
                    <FontAwesome name="plus-circle" size={24} color="black" />
                  </TouchableOpacity>
                </View>
                <View style={{ height: 30, width: 80, margin: 10, }}>
                  <TouchableOpacity onPress={() => removeFromCart(item)} style={{ flex: 1, justifyContent: 'center', alignItems: 'center', borderRadius: 10 }}>
                    <MaterialIcons name="delete" size={24} color="black" />
                  </TouchableOpacity>
                </View>
              </View>
            </View>
          ))}
        </View>
        <View style={{ justifyContent: 'center', alignItems: 'center', marginTop: '10%' }}>
          <Text style={{ fontWeight: 'bold', fontSize: 20,color:'white' }}>Total Pembayaran: ${calculateTotal()}</Text>
          <TouchableOpacity
            style={{ height: 40, width: 120, backgroundColor: '#00FFFF', justifyContent: 'center', alignItems: 'center', marginTop: 10, borderRadius: 10 }}
            onPress={handleCheckOut}
          >
            <Text style={{ color: 'yellow', fontWeight: 'bold' }}>ORDER</Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
      </ImageBackground>
    </View>
  );
};

export default Keranjang;
