import React from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  Clipboard,
  ImageBackground,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

const OrderConfirmation = ({ route, navigation }) => {
  const { nama, amount, bank } = route.params;

  const copyToClipboard = () => {
    Clipboard.setString(bank);
  };

  return (
    <View style={{ flex: 1,  }}>
    <StatusBar style='auto'/>
    <ImageBackground source={require('../assets/baby.jpg')} style={{ flex: 1 }}>
    <View style={{marginTop:'20%',justifyContent:'center',alignItems:'center'}}>
      <Text style={{ fontSize: 20, marginBottom: 20,color:'white',fontWeight:'bold' }}>Order Confirmation</Text>
      </View>
      <View style={{justifyContent:'center',alignItems:'center'}}>
      <Text style={{color:'white'}}>alamat deposit</Text>
      <Text style={{color:'white'}}>Amount: {amount}</Text>
      <Text style=  {{ color : 'white',
    fontSize:20,fontWeight:'bold'}}>Bank: {bank}</Text>
      <TouchableOpacity onPress={copyToClipboard} style={{ marginTop: 20, backgroundColor: 'yellow', padding: 10 }}>
        <Text style={{color : 'red'}}>Copy Bank rekening bank</Text>
      </TouchableOpacity>
      </View>
      <View style={{justifyContent:'center',alignItems:'center',marginTop:'5%'}}>
      <Text style={{color : 'white'}}>proses deposit dan withdraw memakan waktu</Text>
      <Text style={{color : 'white'}}>paling lama 30 menit</Text>
      </View>
      </ImageBackground>
    </View>
  );
};

export default OrderConfirmation;
