import React, {
  createContext,
  useEffect,
  useState,
} from 'react';

import AsyncStorage from '@react-native-async-storage/async-storage';

// Buat konteks AuthContext
    export const AuthContext = createContext();
    
    export const AuthProvider = ({ children }) => {
      const [user, setUser] = useState({
        walletData: null,
        privateKey: null, // Tambahkan privateKey ke dalam nilai default user
      });
    
      // Fungsi untuk memuat data wallet dari penyimpanan lokal saat aplikasi dimulai
      useEffect(() => {
        const loadWalletData = async () => {
          try {
            const storedAddress = await AsyncStorage.getItem('walletAddress');
            const storedPrivateKey = await AsyncStorage.getItem('privateKey');
            
            if (storedAddress && storedPrivateKey) {
              const walletData = {
                address: storedAddress,
                privateKey: storedPrivateKey,
              };
    
              // Set nilai user, termasuk privateKey
              setUser((prevUser) => ({
                ...prevUser,
                walletData,
                privateKey: storedPrivateKey,
              }));
            }
          } catch (error) {
            console.error('Terjadi kesalahan saat memuat data wallet:', error);
          }
        };
    
        loadWalletData();
      }, []);
    
      // Fungsi untuk menyimpan data wallet ke penyimpanan lokal
      const saveWalletData = async (address, privateKey) => {
        try {
          await AsyncStorage.setItem('walletAddress', address);
          await AsyncStorage.setItem('privateKey', privateKey);
          const walletData = { address, privateKey };
    
          // Set nilai user, termasuk privateKey
          setUser((prevUser) => ({
            ...prevUser,
            walletData,
            privateKey,
          }));
        } catch (error) {
          console.error('Terjadi kesalahan saat menyimpan data wallet:', error);
        }
      };
    
      return (
        <AuthContext.Provider value={{ user, setUser, saveWalletData }}>
          {children}
        </AuthContext.Provider>
      );
    };
    