import React from 'react';

import { StatusBar } from 'expo-status-bar';
import {
  Image,
  ImageBackground,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

const SplashScreen = ({navigation}) => {
  return (
    <View style={{
        flex:1
    }}>
    <ImageBackground  source={require('../assets/baby.jpg')}
    style={{
        flex:1
    }}>
    <StatusBar style="auto" />
      <View style={{

      }}>
      <View style={{
        justifyContent:'center',alignItems:'center',
        marginTop:'30%'
      }}>
      <Image source={require('../assets/miner.jpg')}
       style={{
        height:180,width:180,borderRadius:30
       }}/>
      </View>
      </View>
      <View style={{
        justifyContent:'center',
        alignItems:'center',
        marginTop:'5%'
      }}>
      <TouchableOpacity style={{
        justifyContent:'center',alignItems:'center',
        height:40,width:'70%',
        backgroundColor:'#00FFFF',
        borderRadius:50,
        marginTop:'5%',
        margin:5,
        elevation:10
      }}
      onPress={()=>navigation.navigate('Login')}
      >
      <Text style={{
        fontSize:15,color:'red',fontWeight:'bold'
      }}>Login with Email</Text>
      </TouchableOpacity>
      <TouchableOpacity
      style={{
        justifyContent:'center',alignItems:'center',
        height:40,width:'70%',
        backgroundColor:'yellow',
        borderRadius:10,
        marginTop:'5%',
        margin:5,
        elevation:10
      }}
      onPress={()=>navigation.navigate('LoginWallet')}
      >
      <Text
      style={{
        fontSize:15,color:'red',fontWeight:'bold'
      }} 
      >Login with Wallet</Text>
      </TouchableOpacity>
      </View>
      </ImageBackground>
    </View>
  )
}

export default SplashScreen